.gpt3__blog-container_article {
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
    background-color: var(--color-footer);
}

.gpt3__blog-container_article-image {
    width: 100%;
    height: 100%;
    background-color: var(--color-bg);
}

.gpt3__blog-container_article-image img {
    width: 100%;
    height: 100%;
}

.gpt3__blog-container_article-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
    padding: 1rem 1.5rem;
    height: 100%;
}

.gpt3__blog-container_article-content p {
    font-size: 12px;
    line-height: 35px;

    font-weight: bold;
    font-family: var(--font-family);
    color: white;
}

.gpt3__blog-container_article-content h3 {
    font-size: 26px;
    line-height: 30px;

    font-weight: bold;
    font-family: var(--font-family);
    color: white;

    cursor: pointer;
    margin-bottom: 5rem;
}

.gpt3__blog-container_article-content p:last-child {
    cursor: pointer;

}

@media screen and (max-width: 550px){
    .gpt3__blog-container_article-content h3 {
        font-size: 18px;
        line-height: 25px;
    }
}

