.gpt3__possibility {
    display: flex;
    flex-direction: row;

}

.gpt3__possibility-image {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-right: 2rem;
}

.gpt3__possibility-image img {
    width: 100%;
    height: 100%;
}

.gpt3__possibility-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
}

.gpt3__possibility-content h4 {
    font-family: var(--font-family);
    font-size: 16px;
    line-height: 30px;
    font-weight: 500;
    color: #e372ff;
}

.gpt3__possibility-content h4:last-child{
    color: var(--color-text);
    font-weight: 800;
}

.gpt3__possibility-content h1 {
    font-family: var(--font-family);
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    margin-bottom: 2rem ;
}

.gpt3__possibility-content p {
    font-family: var(--font-family);
    font-size: 16px;
    line-height: 30px;
    color: var(--color-text);
    margin-bottom: 2rem;
}

@media screen and (max-width: 950px) {

    .gpt3__possibility {
        display: flex;
        flex-direction: column;
    }

    .gpt3__possibility-image {
    
        margin: 2rem 0;
    }

    .gpt3__possibility-content {
      
        margin-top: 2rem;
    }
}