.gpt3__header {
    display: flex;
    flex-direction: row;
}

.gpt3__header-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;

    margin-right: 5rem;
}

.gpt3__header-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
}

.gpt3__header-content p {
    font-family: var(--font-family);
    font-weight: normal;
    font-size: 20px;
    line-height: 27px;
    color: var(--color-text);
    margin-top: 1.5rem;
}

.gpt3__header-content-input {
    width: 100%;
    margin: 2rem 0 1rem;
    display: flex;
}

.gpt3__header-content-input input{
    flex: 2;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    font-size: 20px;
    line-height: 27px;
    background: var(--color-footer);
    border: 2px solid var(--color-footer);
    padding: 0 1rem;
    outline: none;
    color: rgb(255, 255, 255);

    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}

.gpt3__header-content-input button{
    flex: 0.6;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    font-size: 15px;
    line-height: 27px;
    background: var(--color-button);
    border: 0.5px solid var(--color-footer);
    padding: 0 1rem;
    outline: none;
    color: rgba(255, 255, 255, 0.643);
    cursor: pointer;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
}

.gpt3__header-content-people {
    display: flex;
    width: 100%;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;
    margin-top: 2rem;
}
.gpt3__header-content-people img {
    width: 181px;
    height: 38px;
}

.gpt3__header-content-people p {
    margin: 0;
    margin-left: 1rem;
    font-family: var(--font-family);
    font-weight: 200;
    color: white;
    font-size: 12px;
    line-height: 38px;
    text-align: center;
}

.gpt3__header-image {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;

}

.gpt3__header-image img{
    width: 100%;
    height: 100%;  
}

@media screen and (max-width: 1050px) {
    .gpt3__header {
        flex-direction: column;
    }

    .gpt3__header-content {
        margin: 0 0 3rem;
    }
}

@media screen and (max-width: 650px) {
    .gpt3__header h1{
        font-size: 48px;
        line-height: 60px;
    }
    .gpt3__header p{
        font-size: 16px;
        line-height: 24px;
    }

    .gpt3__header-content-people {
        flex-direction: column;
    }
    .gpt3__header-content-people p{
        margin: 1rem 0;
    }

    .gpt3__header-content-input input, 
    .gpt3__header-content-input button {
        font-size: 16px;
        line-height: 24px;
    }
}

@media screen and (max-width: 490px) {
    .gpt3__header h1{
        font-size: 36px;
        line-height: 48px;
    }

    .gpt3__header p{
        font-size: 14px;
        line-height: 20px;
    }

    .gpt3__header-content-people {
        flex-direction: column;
    }
    .gpt3__header-content-people p{
        margin: 1rem 0;
    }

    .gpt3__header-content-input input, 
    .gpt3__header-content-input button {
        font-size: 14px;
        line-height: 20px;
    }
}